@import "../../sass/helpers/variables";
@import "../../sass/helpers/placeholders";

.header {
  width: 100%;
  max-width: 1440px;
  margin: $margin-center;
  padding: 2.29% 11.67% 1.88%;
  position: absolute;
  top: 17px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 1;

  .header__box {
    width: 19.64%;
    display: flex;
    gap: 12%;

    .header__language {
      @extend %hover-animation;
      @extend %transparent-button;
      width: 42.4%;
      padding: 0;
      display: flex;
      align-items: center;

      .header__flag {
        width: 32px;
        height: 32px;
        padding-right: 11px;
      }

      .header__name {
        @extend %font-regular;
        @extend %text-caps-white;
        font-size: 14px;
        line-height: 1.21;
        margin: 0;
        padding-right: 15.4%;
      }

      .header__arrow {
        width: 16px;
        height: 16px;
      }
    }

    .header__login {
      @extend %font-regular;
      @extend %hover-animation;
      @extend %button-decoration;
      @extend %text-caps-white;
      max-width: 100px;
      padding: 5.5% 13.4%;
      background: $transparent;
      border: 1px solid $white;
    }
  }
}

@media screen and (max-width: 1180px) {
  .header {
    .header__box {
      width: 25%;

      .header__language {
        .header__flag {
          width: 30px;
          height: 30px;
        }

        .header__name {
          font-size: $button-medium;
        }

        .header__arrow {
          width: 15px;
          height: 15px;
        }
      }

      .header__login {
        font-size: $button-medium;
      }
    }
  }
}

@media screen and (max-width: 960px) {
  .header {
    .header__box {
      width: 33%;

      .header__language {
        .header__flag {
          width: 27px;
          height: 27px;
          padding-right: 8px;
        }

        .header__name {
          font-size: $button-small;
        }

        .header__arrow {
          width: 14px;
          height: 14px;
        }
      }

      .header__login {
        font-size: $button-small;
      }
    }
  }
}

@media screen and (max-width: 580px) {
  .header {
    .header__box {
      width: 38%;

      .header__language {
        .header__flag {
          width: 22px;
          height: 22px;
          padding-right: 5px;
        }

        .header__name {
          font-size: $button-mobile;
        }

        .header__arrow {
          display: none;
        }
      }

      .header__login {
        font-size: $button-mobile;
      }
    }
  }
}
